import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Vin001Component } from './vin001/vin001.component';
import { Vin002Component } from './vin002/vin002.component';
import { Vin003Component } from './vin003/vin003.component';
import { Vin004Component } from './vin004/vin004.component';
import { Vin005Component } from './vin005/vin005.component';
import { Vin006Component } from './vin006/vin006.component';
import { SecurityAccessGuard } from '../../common/guards/security-access.guard';

const routes: Routes = [
  {
    path: 'vin001',
    component: Vin001Component,
    // canActivate: [SecurityAccessGuard],
    // canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'VIN001'
    }
  },
  {
    path: 'vin002',
    component: Vin002Component,
    // canActivate: [SecurityAccessGuard],
    // canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'VIN002'
    }
  },
  {
    path: 'vin003',
    component: Vin003Component,
    // canActivate: [SecurityAccessGuard],
    // canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'VIN003'
    }
  },
  {
    path: 'vin004',
    component: Vin004Component,
    // canActivate: [SecurityAccessGuard],
    // canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'VIN004'
    }
  },
  {
    path: 'vin005',
    component: Vin005Component,
    // canActivate: [SecurityAccessGuard],
    // canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'VIN005'
    }
  },
  {
    path: 'vin006',
    component: Vin006Component,
    // canActivate: [SecurityAccessGuard],
    // canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'VIN006'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VinculacionRoutingModule { }
