  filterCorregimiento(valor: string): (item) => boolean {
    return (item) => {
      if (valor === item.code_provincia) {
        return true;
      }
      return false;
    };
  }

  filterDistrito(valor: string, valor2: string): (item) => boolean {
    return (item) => {
      if (valor === item.code_provincia && valor2 === item.code_canton) {
        return true;
      }
      return false;
    };
  }

