  filterCanton( provincia: string ): (item) => boolean {
    return (item) => {
      return item.code_provincia === provincia;
    };
  }

  filterCantonProvincia( provincia: string, canton: string ): (item) => boolean {
    return (item) => {
      return item.code_provincia === provincia && item.code_canton === canton;
    };
  }

  getCatalogoCR( KEY: string): void {
    this.catalogoCR[KEY] = [];
    this.catalogo.data(KEY, this.params([PAIS, MODULO, LENGUAJE])).subscribe( response => {
      this.catalogoCR[KEY] = response ? response : [];
    });
  }

  filtroCR( value: string ): (item) => boolean {
    return (item) => item.value.startsWith(value);
  }

