import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { SearchPipe } from 'src/app/modules/common/pipe/search/search.pipe';

@Component({
  selector: 'app-search-list',
  templateUrl: './search-list.component.html',
  styleUrls: ['./search-list.component.css']
})
export class SearchListComponent implements OnInit {

  lista: boolean;
  valor: string;

  @Input() formulario: FormGroup;
  @Input() ControlName: string;
  @Input() set contenido(c: Array<any>) {
    this.cont = c;
    this.ngOnInit();
  }
  cont: Array<any>;
  @Input() setValue: (item) => string;
  @Input() filtro: (item) => string;
  @Input() placeholder?: string;

  @Input() set InitialValue( va: string) {
    this.initialValue = va;
    this.ngOnInit();
  }
  initialValue: string;

  @Output() change = new EventEmitter();
  @Input() setLabel: (item) => string;

  constructor() {
    this.setValue = (item) => '';
    this.setLabel = (item) => '';
    this.lista = false;
    this.valor = '';
    this.cont = [];
    setTimeout(() => this.formulario.get(this.ControlName).setValue(''), 150);
  }

  ngOnInit() {
    const value: Array<any> = this.cont.filter(
      item => {
        if ( this.initialValue === this.setValue(item)) {
          return true;
        }
        return false;
      }
    );
    if ( value.length > 0 ) {
      this.set(value[0]);
    }
  }

  ocultarLista(): void {
    setTimeout( () => this.lista = false, 250 );
  }

  ocutar(): void {
    this.lista = false;
  }

  mostrarLista(): void {
    this.lista = true;
  }

  touch(): void {
    this.formulario.get(this.ControlName).markAsTouched();
    this.formulario.get(this.ControlName).markAsDirty();
  }

  search(event) {
    this.valor = event.target.value;
    this.formulario.get(this.ControlName).setValue(this.valor);
  }

  set(item: any): void {
    this.valor = this.setLabel(item);
    this.formulario.get(this.ControlName).setValue(this.setValue(item));
    this.change.emit(item);
    this.touch();
  }

  fil(searchInArray, valor, filtro): Array<any> {
    return new SearchPipe().transform(searchInArray, valor, filtro);
  }

}
