import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { SendInformationService } from 'src/app/modules/common/service/SendInformation/send-information.service';
import { WorkflowService } from 'src/app/modules/common/service/workflow/workflow.service';
import { PAYLOAD, PAIS, MODULO, LENGUAJE } from 'src/app/modules/common/CONST';
import { ViewEncapsulation } from 'src/app/modules/common/components/encaptulation';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { TIPO_IDENTIFICACION } from 'src/app/modules/common/CONST';

@Component({
  selector: 'app-vin001',
  templateUrl: './vin001.component.html',
  styleUrls: ['./vin001.component.scss', '../../../../../assets/cam/scss/components/vinculacion/_vin001.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Vin001Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {
  typeIdentification: any;
  genero: { M: string; F: string; };
  tipoID: string;

  ngOnInit(): void {
    this.genero = { M: 'Masculino', F: 'Femenino' };
    this.typeIdentification = [];
    this.catalogo.data('ING002', this.params([PAIS, MODULO, LENGUAJE])).subscribe(
      response => {
          this.typeIdentification = response ? response : [];
      }
    );
  }

  getTipoId(tipo: string): string {
    const vall = (this.typeIdentification as Array<any>).filter( item => {
      if ( tipo === item.value) {
        return true;
      }
      return false;
    });
    if ( vall.length === 1) {
      return vall[0].label;
    }
    return 'CC';
  }

  ngOnDestroy(): void {

  }

  getDataToPayload(data?: any): () => ({ [key: string]: any; }) {
    this.disableButton = true;
    return (): { [key: string]: any; } => ({});
  }

  getDataToBackButton(data?: any): () => ({ [key: string]: any; }) {
    return (): { [key: string]: any; } => ({
      flujo: {
        atras: true,
        continuar: false,
        callback: () => { }
      }
    });
  }
}
