  getTipoOperacionesPA(): void {
    this.tipoOperacionesPA = [];
    this.catalogo.data('TIPO_OPERACION', this.params([PAIS, MODULO, LENGUAJE])).subscribe( response => {
      this.tipoOperacionesPA = response ? response : [];
    });
  }

