  /* PENDIENTE REVISAR EL SELECT REPETIDO DE OTRAS NACIONALIDADES */
  /* Honduras */
  makeFormGroupHN() {
    const R = this.rules;
    this.formulario = this.formBuilder.group({
      ciudadaniaUsaHN: [false, []],
      nacionalidadHN: ['', [
        R.resetMyCallbackValidation([
          'numeroPasaporteHN',
          'pasaporteUsaHN',
          'numeroPasaporteHN',
          'residenciaHN',
          'numeroTinHN'
        ])
      ]],
      nacionalidadUsaHN: [false, [
        R.resetMyCallbackValidation([
          'pasaporteUsaHN',
          'numeroPasaporteHN',
          'residenciaHN',
          'numeroTinHN'
        ])
      ]],
      pasaporteUsaHN: [false, [
        R.resetMyCallbackValidation([
          'numeroPasaporteHN'
        ])
      ]],
      numeroPasaporteHN: ['', [
        R.validationWithCallBack(R.errorTypeRequired, (control: FormGroup) => {
          if ( !control.parent ) {
            return true;
          }
          if ( control.parent.get('pasaporteUsaHN').value === true &&
                control.parent.get('nacionalidadUsaHN').value === false
          ) {
              return control.value !== '';
          }
          return true;
        }),
        R.validationForMinLength(6),
        R.validationForMaxLength(20)
      ]],
      residenciaHN: [false, [
        R.resetMyCallbackValidation([
          'numeroTinHN'
        ])
      ]],
      numeroTinHN: ['', [
        R.validationWithCallBack(R.errorTypeRequired, (control: FormGroup) => {
          if ( !control.parent ) {
            return true;
          }
          if ( control.parent.get('nacionalidadUsaHN').value === true ||
                control.parent.get('residenciaHN').value === true ||
                control.parent.get('nacionalidadHN').value === '00008'
          ) {
              return control.value !== '';
          }
          return true;
        }),
        R.validationForOnlyNumbers(),
        R.validationForMinLength(9),
        R.validationForMaxLength(20)
      ]],
      diasAnioCursoHN: ['', [
        R.validationForOnlyNumbers()
      ]], // Numerico, si el cliente no digita nada debe ser 0
      diasAnioPasadoHN: ['', [
        R.validationForOnlyNumbers()
      ]], // Numerico, si el cliente no digita nada debe ser 0
      diasAnioAntepasadoHN: ['', [
        R.validationForOnlyNumbers()
      ]], // Numerico, si el cliente no digita nada debe ser 0
      estudioHN: [false, []], // Solo 1 opcion Required
      activdadDiplomaticaHN: [false, []], // Solo 1 opcion Required
      tratamientoMedicoHN: [false, []], // Solo 1 opcion Required
      otroMotivoHN: [false, []], // Solo 1 opcion Required, si selecciona esta opcion desplegar campo de descripcion de motivo
      nacionalidad2HN: [false, []] // Esta no va, validar, agregar campo de descripcion de motivo STRING, MIN 1, MAX 50
    });
    this.FC = this.formulario.controls;
  }
