import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'app-check-sino',
  templateUrl: './check-sino.component.html',
  styleUrls: ['./check-sino.component.sass']
})
export class CheckSinoComponent implements OnInit {

  @Output() checkChange = new EventEmitter();
  @Input() status: boolean;
  constructor() { }

  ngOnInit() {
  }

  onChange(statusCheck: boolean) {
    this.checkChange.emit({ statusCheck });
  }
}
